html,body 
#potato{
height: 100%;
}
body{
margin:0;
padding: 0;
background-color: #eff3f7;
}
.container{
height: 100%;
display: flex;
justify-content:center;
}
.loader{
width:100%;
height: 100%;
display: flex;
justify-content: center;
align-items: center;
}

.movies {
padding-top: 70px;
width: 80%;
display: flex;
justify-content: space-between;
align-items: flex-start;
flex-wrap: wrap;
padding: 50px;
}

.movies .movie{
    width: 45%;
    background-color: white;
    margin-bottom: 70px;
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
}

.movie img {
    position: relative;
    top: -50px;
    max-width: 150px;
    width: 100%;
    margin-right: 30px;
}

.movie h3,
.movie h5{
 margin: 0;
 font-weight: 300;
}

.movie h3{
    margin-bottom: 5px;
    font-size: 24px;
}

.movie h5,
.movie .movie__year{
    opacity: 0.3;
}